import { UseCaseResult } from "../../core/app/use-cases";
import { Container } from "../../di/container";
import { z as zod } from "zod";

const MonthStringSchema = zod
  .string()
  .regex(/^(0[1-9]|1[0-2])\.\d{4}$/, "  (MM.YYYY)");

const showStatus = (status: UseCaseResult) => {
  const statusEl = document.getElementById("status")!;
  statusEl.textContent = status.message;
  statusEl.style.display = "block";
  statusEl.style.color = status.isError() ? "red" : "green";
};

const handleCreatePayroll = async (e) => {
  e.preventDefault();

  const month = (document.getElementById("month") as HTMLInputElement).value;
  const validationResult = MonthStringSchema.safeParse(month);
  if (!validationResult.success) {
    showStatus(UseCaseResult.ValidationError(validationResult.error.message));
    return;
  }

  const createSummaryPayrollUseCase = new Container().createSummaryPayrollUseCase(month);

  const result = await createSummaryPayrollUseCase.generateSummary(month);
  showStatus(result);
};

const handleVisitorsValidation = async (e) => {
  e.preventDefault();

  const month = (document.getElementById("visitorsMonth") as HTMLInputElement).value;
  const validationResult = MonthStringSchema.safeParse(month);
  if (!validationResult.success) {
    showStatus(UseCaseResult.ValidationError(validationResult.error.message));
    return;
  }

  const firstRow = (document.getElementById("visitorsFirstRow") as HTMLInputElement).value;

  const validateVisitorsUseCase = new Container().createVisitorsValidationUseCase(
    month,
    Number(firstRow)
  );

  const result = await validateVisitorsUseCase.validateVisitors(month);
  showStatus(result);
};
const handleCheckPayroll = async (e) => {
  e.preventDefault();

  const month = (document.getElementById("monthPayroll") as HTMLInputElement).value;
  const validationResult = MonthStringSchema.safeParse(month);
  if (!validationResult.success) {
    showStatus(UseCaseResult.ValidationError(validationResult.error.message));
    return;
  }

  const firstRow = (document.getElementById("firstRow") as HTMLInputElement).value;

  const createSummaryPayrollUseCase = new Container().createCheckPayrollUseCase(
    month,
    Number(firstRow)
  );

  const result = await createSummaryPayrollUseCase.checkUnitPayroll(month);
  showStatus(result);
};

Office.onReady((info) => {
  if (info.host === Office.HostType.Excel) {
    const form = document.getElementById("summaryForm")!;
    const payrollForm = document.getElementById("payrollForm")!;
    const visitorsValidationForm = document.getElementById("visitorsValidationForm")!;
    const main = document.getElementById("app-body")!;

    main.style.display = "block";

    form.addEventListener("submit", handleCreatePayroll);
    payrollForm.addEventListener("submit", handleCheckPayroll);
    visitorsValidationForm.addEventListener("submit", handleVisitorsValidation);
  }
});
